import { listPopShadeMixin } from '@/common/utils.js'


export default {
	
	data() {
		return {
			//聊天列表
			userList: []
			
		}
	},
	mixins: [listPopShadeMixin],
	methods: {
		
		
		
		
		/* 获取列表数据 */
		getListData() {
			let list = [];
			for (let i = 0; i < 20; i++) {
				list.push({
					"name": `第${i+1}个用户`,
					"time": '3月18日',
					"msg": `这是第${i+1}个用户的聊天信息这是第${i+1}个用户的聊天信息这是第${i+1}个用户的聊天信息`
				})
			}
			this.userList = list;
		},
		
		//点击菜单的下标index
		choosePop(index) {
			return index
		},
		
		//跳转到session
		toSession(name) {
			uni.navigateTo({
				url: `/components/content/session/Session?name=${name}`
			})
		}
		
		
		
	},
	beforeMount() {
		this.getListData()
	}
	
}
